import React from 'react';

interface AboutMeProps {
    bio: string;
    skills: string[];
    achievements: string[];
}

export default function AboutMe({ bio, skills, achievements}: AboutMeProps) {
    return (
      <section className="my-12 p-6 bg-white dark:bg-gray-900 rounded-lg shadow-md max-w-3xl mx-auto">
        <h2 className="text-3xl font-semibold mb-4 border-b-2 border-indigo-500 inline-block">About Me</h2>
        <p className="text-gray-700 dark:text-gray-300 mb-6">{bio}</p>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <h3 className="text-2xl font-semibold mb-2">Skills</h3>
            <div className="flex flex-wrap gap-2">
              {skills.map((skill, index) => (
                <span
                  key={index}
                  className="bg-indigo-100 text-indigo-800 dark:bg-indigo-800 dark:text-indigo-100 px-3 py-1 rounded-full text-sm"
                >
                  {skill}
                </span>
              ))}
            </div>
          </div>
          <div>
            <h3 className="text-2xl font-semibold mb-2">Achievements</h3>
            <div className="flex flex-wrap gap-2">
              {achievements.map((achievement, index) => (
                <span
                  key={index}
                  className="bg-green-100 text-green-800 dark:bg-green-800 dark:text-green-100 px-3 py-1 rounded-full text-sm"
                >
                  {achievement}
                </span>
              ))}
            </div>
          </div>
        </div>
      </section>
    );
  }